const injectTopBanner = (page, links, setElementsCallback, timeout = 20000) => {
    if (document.getElementById("malwarebytes-root")) {
        console.log("Already displaying a notification");
        return;
    }

    if (window.self !== window.top) {
        return;
    }

    let newDiv = null;
    let displayNotification = function () {
        // Create the element
        newDiv = document.createElement("div");
        newDiv.tabIndex = -1; // Keeps it out of tab order

        // --- Positioning and Sizing ---
        newDiv.style.position = "fixed";// Takes it out of flow, positions relative to viewport, enables z-index, creates stacking context.
        newDiv.style.bottom = "auto";   // Positions the bottom edge automatically
        newDiv.style.right = "0";       // Positions the right edge at the viewport right
        newDiv.style.top = "0";         // Positions the top edge at the viewport top
        newDiv.style.left = "0";        // Positions the left edge at the viewport left
        newDiv.style.width = "100%";    // Makes it full viewport width

        // --- Stacking ---
        newDiv.style.zIndex = "2147483647"; // Max z-index.

        // --- Identification & Content ---
        newDiv.id = "malwarebytes-root"; // Useful ID
        const shadow = newDiv.attachShadow({ mode: "closed" }); // Creates Shadow DOM. Content inside is encapsulated. The host (`newDiv`) participates in the page stacking.

        // --- Insertion ---
        document.body.insertBefore(newDiv, document.body.firstChild); // Inserts directly into <body> *before* other elements. Generally good for overlays.

        fetch(chrome.runtime.getURL(page))
            .then((resp) => resp.text())
            .then((html) => {
                try {
                    for (const link of links) {
                        let linkElement = document.createElement(link.type);
                        linkElement.setAttribute("rel", "stylesheet");
                        linkElement.setAttribute("href", link.ref);
                        if (link.media) {
                            linkElement.setAttribute("media", link.media);
                        }
                        shadow.appendChild(linkElement);
                    }
                } catch (error) {
                    console.error("Error while injecting links", error);
                }

                const messageHtml = new DOMParser().parseFromString(html, "text/html");
                let containerDiv = document.createElement("div");
                shadow.appendChild(containerDiv);
                containerDiv.className = "top-container";
                containerDiv.style.display = "flex";
                containerDiv.style.padding = "10px";
                containerDiv.style.right = "0";
                containerDiv.style.position = "absolute";
                let rowDiv = document.createElement("div");
                rowDiv.className = "row-container";
                rowDiv.style.width = "100%";
                rowDiv.style.display = "flex";
                rowDiv.style.margin = "5 5 5 5";
                let rightFillerDiv = document.createElement("div");
                let displayDiv = document.createElement("div");
                containerDiv.appendChild(rowDiv);
                rowDiv.appendChild(rightFillerDiv);
                rowDiv.appendChild(displayDiv);
                rightFillerDiv.style.flex = "1";
                displayDiv.style.flex = "0 0 auto";
                displayDiv.appendChild(messageHtml.body);

                const closeBtn = shadow.querySelector("#mb-close");
                closeBtn && closeBtn.addEventListener("click", () => {
                    while (document.getElementById("malwarebytes-root")) {
                        document.getElementById("malwarebytes-root").remove();
                    }
                    newDiv = null;
                });
                setElementsCallback(shadow);
            })
            .catch((err) => {
                console.error("Error while fetching notification page", err);
            });

        // create a timer to destroy the div after 5 seconds
        let timer = setTimeout(function () {
            // destroy the div element if it is not already destroyed by close button
            let div = document.getElementById("malwarebytes-root");
            div && div.remove();
            clearTimeout(timer);
            newDiv = null;
        }, timeout);
    };
    displayNotification();
};

export function blockContentAndContinue(src, malware, tabId) {
    const links = [
        {
            type: "link",
            ref: chrome.runtime.getURL("app/eventpages/block-notification.css"),
            media: "",
        },
        {
            type: "link",
            ref: chrome.runtime.getURL("app/eventpages/block-notification-dark.css"),
            media: "(prefers-color-scheme: dark)",
        },
        {
            type: "link",
            ref: "https://fonts.googleapis.com/css?family=Roboto&display=swap",
            media: "",
        },
    ];

    injectTopBanner(
        "app/eventpages/block-notification.html",
        links,
        (body) => {
            body.querySelector("#site-url").innerText = " " + src;
            body.querySelector("#malware-url").innerText = " " + malware;
            translateText(
                [
                    { id: "block-notification-title", msg: "blockNotificationTitle" },
                    { id: "block-notification-message", msg: "blockNotificationMessage" },
                    {
                        id: "block-notification-site-label",
                        msg: "blockNotificationSiteLabel",
                    },
                    {
                        id: "block-notification-malware-label",
                        msg: "blockNotificationMalwareLabel",
                    },
                ],
                body
            );
        },
        20000
    );
    setTimeout(() => {
        const msg = { type: MSG_INFO_BLOCKED_CONTENT_DISPLAYED };
        msg["payload"] = { host: src, tabId: tabId };
        chrome.runtime.sendMessage(msg);
    }, 1000);
}

export function displayMonthlyNotification() {
    let message = { type: MSG_RECORDS_GET };
    message["payload"] = {
        min: Date.today().addDays(-30).toString("MMM d yyyy"),
        max: Date.today().toString("MMM d yyyy"),
    };

    chrome.runtime.sendMessage(message, function (response) {
        if (!response || response.error) {
            console.log(
                "Error calling getRecords: ",
                response && response.error ?
                    response.error :
                    "No error response received"
            );
            if (typeof response.error === "object") {
                console.error(response.error);
            }
        } else {
            const recArr = Object.entries(response.success["records"]);
            let totalAds = recArr.reduce((acc, item) => {
                return acc + item[1].ads;
            }, 0);
            let totalMalwares = recArr.reduce((acc, item) => {
                return acc + item[1].malwares;
            }, 0);
            let totalScams = recArr.reduce((acc, item) => {
                return acc + item[1].scams;
            }, 0);
            let totalContent = recArr.reduce((acc, item) => {
                return acc + (item[1].content || 0);
            }, 0);

            const links = [
                {
                    type: "link",
                    ref: chrome.runtime.getURL("app/eventpages/monthly-notification.css"),
                    media: "",
                },
                {
                    type: "link",
                    ref: chrome.runtime.getURL(
                        "app/eventpages/monthly-notification-dark.css"
                    ),
                    media: "(prefers-color-scheme: dark)",
                },
                {
                    type: "link",
                    ref: "https://fonts.googleapis.com/css?family=Roboto&display=swap",
                    media: "",
                },
            ];
            injectTopBanner(
                "app/eventpages/monthly-notification.html",
                links,
                (body) => {
                    const totalStats = [
                        { key: 'ads', total: totalAds },
                        { key: 'malware', total: totalMalwares },
                        { key: 'scams', total: totalScams },
                        { key: 'content', total: totalContent }];
                    totalStats.forEach((stat) => {
                        body.querySelector(`#${stat.key}`).innerText = stat.total.toLocaleString();
                        stat.total === 0 && body.querySelector(`#${stat.key}`).classList.add("disabled");
                    });
                },
                86400 * 1000
            );
        }
    });
}

