export function displayReleaseNotification(cardContent) {
    const links = [
        {
            type: "link",
            ref: chrome.runtime.getURL("app/eventpages/release-notification.css"),
            media: "",
        },
        {
            type: "link",
            ref: chrome.runtime.getURL(
                "app/eventpages/release-notification-dark.css"
            ),
            media: "(prefers-color-scheme: dark)",
        },
        {
            type: "link",
            ref: "https://fonts.googleapis.com/css?family=Roboto&display=swap",
            media: "",
        },
    ];

    injectTopBanner(
        "app/eventpages/release-notification.html",
        links,
        (body) => {
            const cardTitle = body.getElementById("mb-card-text-title");
            cardTitle.innerText = `What's new in v${chrome.runtime.getManifest().version}`;
            const cardBody = body.getElementById('mb-card-text-body');
            if (cardContent && Array.isArray(cardContent) && cardContent.length > 0) {
                const list = document.createElement('ul');
                cardBody.appendChild(list);
                cardContent.forEach((content) => {
                    const listItem = document.createElement('li');
                    const listContainer = document.createElement('div');
                    listItem.appendChild(listContainer);
                    const listIcon = document.createElement('img');
                    listIcon.alt = 'Check icon';
                    listIcon.src = chrome.runtime.getURL('app/assets/check-icon.svg');
                    const listText = document.createElement('span');
                    listText.innerText = content;
                    listContainer.appendChild(listIcon);
                    listContainer.appendChild(listText);
                    list.appendChild(listItem);
                });
                appendPermissionsWarningIfNeeded(body);
            } else {
                cardBody.innerText = RELEASE_NOTIFICATION_DEAFULT_TEXT;
            }
        },
        20000
    );
}

function dateToReadableString(dateInput, locale) {
    // Ensure the input is a Date object
    let date = new Date(dateInput);
    
    // Check if the date is invalid
    if (isNaN(date)) {
        return 'Invalid Date';
    }

    // Define options for formatting
    let options = {
        year: 'numeric',
        month: 'long',
        day: 'numeric'
    };
    
    // Format the date using toLocaleDateString with the provided locale
    let formattedDate = date.toLocaleDateString(locale, options);

    // remove ',' from the date, Extract month, day, and year components from formatted date
    const dateParts = formattedDate.replace(/,/g, '').split(' ');
    
    // Format the date in the desired format "Month, day Year"
    if (dateParts.length === 3) {
        return `${dateParts[0]} ${dateParts[1]}, ${dateParts[2]}`;
    } else if (dateParts.length === 4) {
        return `${dateParts[0]} ${dateParts[1]}, ${dateParts[3]}`;
    } else {
        return formattedDate;
    }
}

