import DOMPurify from 'dompurify';
import {
    DB_NAMES_ALL, DB_NAMES_BLOOM,
    DB_NAMES_RAW, DB_NAME_FEATURE_FLAGS,
} from "../../utils/databases/db-consts.js";
import breachesDB from "@/db/breaches.json";
import { loadCachedDatabases } from "../../utils/databases/db-persistence";
import {
    displayMonthlyNotification, 
    displayReleaseNotification, 
    displaySearchHijackingNotification, 
    displayShellInjectionNotification,
} from "../../content-scripts/content-notifications.js";
import { chrome } from "../../utils/polyfill.ts";
import { simpleStorageGet } from '../../utils/storage.ts';
import {
    MSG_DEBUG_TEST_CHANNEL,
    MSG_DEBUG_STAGING_CHANNEL,
    MSG_DEBUG_STAGING_CHANNEL_RESPONSE,
    MSG_DEBUG_UPDATE_ERROR,
    RELEASE_NOTIFICATION_NOTES_MV3,
    SETTING_NEW_RELEASE_NOTIFICATION,
    MSG_DEBUG_CHECK_URL,
    MSG_DEBUG_ADD_HEURISTIC_RULE,
    MSG_TEST_MY_ACCOUNT_RESPONSE,
    MSG_TEST_FP_CHECKER
} from "./app-consts.js";

import {
    settingsGetAsync,
    downloadJSONfile,
    removeAccessPermissions,
    requestAccessPermissions
} from "./ui-utils/ui-utils.js";

import { getExistingDynamicRules } from "./mv3/dynamic-rule-utils";

const attachEvents = (dom) => {
    dom.getElementById("mb-disable-shell-warning").addEventListener("click", async () => {
        const popup = document.getElementById("malwarebytes-root");
        popup && popup.remove();
    });
};

window.addEventListener("load", async () => {
    const action = document.getElementById("action");
    const params = new URL(document.location).searchParams;
    const iframeLoader = document.getElementById("iframe-loader");
    let urlQuery = "";

    switch (params.get("action")) {
        case "user-group-a":
            action.innerText = "User Group changed to A";
            break;
        case "user-group-b":
            action.innerText = "User Group changed to B";
            break;
        case 'get-dynamic-rules': {
            action.innerText = "Getting dynamic rules";
            const rules = await getExistingDynamicRules();
            // Download the rules as a json file
            const fileName = `dynamic-rules-${new Date().toISOString().replace(/:/g, '-').split('.')[0]}.json`;
            downloadJSONfile(rules, fileName);
            action.innerText = "Dynamic rules downloaded";
            break;
        }
        case "my-data":
            action.innerText = "Logged my debug data";
            break;
        case "full-stats":
            action.innerText = "Stat graph populated";
            break;
        case "populate-detection-history":
            action.innerText = "Detection History populated";
            break;
        case "toggle-reputation-on":
            action.innerText = "Reputation database toggled ON";
            break;
        case "toggle-reputation-off":
            action.innerText = "Reputation database toggled OFF";
            break;
        case "toggle-local-ip-allow-on":
            action.innerText = "Local IP Allowlisting is ON";
            break;
        case "toggle-local-ip-allow-off":
            action.innerText = "Local IP Allowlisting is OFF";
            break;
        case "factory-reset":
            action.innerText = "Clear Browser Guard storage action triggered";
            break;
        case "test-channel-update":
            action.innerText = "Test channel update triggered";
            debugTestChannelUpdate();
            break;
        case "staging-channel-update":
            action.innerText = "Staging channel update triggered";
            debugStagingChannelUpdate();
            break;
        case "send-test-user-telemetry":
            action.innerText = "Sent test user action telemetry. Check the network inspector for the request.";
            break;
        case "reset-monthly-notification-date":
            action.innerText = "The monthly notification date reset";
            break;
        case "trigger-monthly-notification": {
            const iframe = document.createElement("iframe");
    
            action.innerText = "The monthly notification triggered";
            iframeLoader.style.margin = "35px";
            iframe.title = "Debug iframe"
            iframe.dataset.src = "https://www.youtube.com/embed/fbJ4o_Yj70E?playlist=5wn5M-F9UOk,-XBYNb7DUYg,74TND1mElRM,NDv2P9zLp8Y,ofeeUlFnqZc,E0d7ElpE4ic,frsCumJyCxM,S4v6oRT6hR4,DcWKBJWDLcQ"
            iframe.allow = "accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
            iframe.src = "https://www.youtube.com/embed/fbJ4o_Yj70E?playlist=5wn5M-F9UOk,-XBYNb7DUYg,74TND1mElRM,NDv2P9zLp8Y,ofeeUlFnqZc,E0d7ElpE4ic,frsCumJyCxM,S4v6oRT6hR4,DcWKBJWDLcQ";
            iframe.width = "1000";
            iframe.height = "563";
            iframeLoader.appendChild(iframe);
            displayMonthlyNotification();
            break;
        }
        case "trigger-search-hijacking-notification": {
            displaySearchHijackingNotification(attachEvents);
            action.innerText = "Search hijacking notification triggered";
            break;
        }
        case "trigger-release-notification": {
            settingsGetAsync(SETTING_NEW_RELEASE_NOTIFICATION).then((isNewReleaseNotificationEnabled) => {
                if (isNewReleaseNotificationEnabled) {
                    action.innerText = "New release notification triggered (default message)";
                    displayReleaseNotification();
                } else {
                    action.innerText = "New release notification is disabled";
                }
            });
            break;
        }
        case "trigger-release-notification-notes": {
            const release_notes = RELEASE_NOTIFICATION_NOTES_MV3;
            settingsGetAsync(SETTING_NEW_RELEASE_NOTIFICATION).then((isNewReleaseNotificationEnabled) => {
                console.log("isNewReleaseNotificationEnabled", isNewReleaseNotificationEnabled)
                if (isNewReleaseNotificationEnabled) {
                    action.innerText = "New release notification triggered (release notes)";
                    displayReleaseNotification(release_notes);
                }else{
                    action.innerText = "New release notification is disabled";
                }
            });
            break;
        }
        case "trigger-shell-notification":
            action.innerText = "The monthly notification triggered";
            displayShellInjectionNotification(attachEvents);
            break;
        case "set-feature":
            action.innerText = `Set feature ${params.get("key")} to ${params.get("value")}`;
            break;
        case "whitelist-malware-for-browserguard-local":
            action.innerText = "Whitelist malware for https://browserguard.local";
            break;
        case "get-dbs-info": {
            action.innerText = "Downloading DBs info...";

            let dbNames = params.get('dbnames')?.split(',');
            dbNames = dbNames?.length === 0 ? undefined : dbNames;

            let dbGroup = params.get('dbgroup');
            switch (dbGroup) {
                case 'DB_NAMES_BLOOM':
                    dbGroup = DB_NAMES_BLOOM
                    break;
                case 'DB_NAMES_RAW':
                    dbGroup = DB_NAMES_RAW;
                    break;
                case 'DB_NAME_FEATURE_FLAGS':
                    dbGroup = DB_NAME_FEATURE_FLAGS
                    break;
            }

            let keyword = dbGroup || dbNames || DB_NAMES_ALL;
            loadCachedDatabases(keyword).then((dbsInfo) => {
                
                if (dbsInfo.length === 0) {
                    action.innerText = `No DBs found for: ${keyword}`;
                    return;
                }
                dbsInfo.unshift({ "query-meta": params.toString()});
                
                dbsInfo.forEach((db, index) => {
                    delete db.data;
                    displayFeedbackMessage("* " + JSON.stringify(db), `db-${index}`);
                });
                
                action.innerText = "DBs information:";
                
                const fileName = `dbs-info-${new Date().toISOString().replace(/:/g, '-').split('.')[0]}.json`;
                downloadJSONfile(dbsInfo, fileName);
            })

            break;
        }

        case "override-ads-telemetry-duration": {
            action.innerText = "Overriding ads telemetry sending duration";
            break;
        }

        case "trigger-sending-block-telemetry-now": {
            action.innerText = "Triggering sending block telemetry now";
            break;
        }

        case "add-heuristic": {
            action.innerText = "Enter the rule...";
            urlQuery = '{"id": 666, "r":"#?#title:contains(netflix jobs)", "s":false, "t":"phishing", "a": true}';
            urlQuery = prompt("Enter the rule to check", urlQuery);
            addHeuristicRule(urlQuery);
            break;
        }
    
        case "check-url": {
            action.innerText = "Checking url...";
            urlQuery = params.get("q");
            urlQuery = prompt("Enter the URL to check", urlQuery);
            checkUrl(urlQuery);
            break;
        }        

        case "enable-visual-debugging": {
            action.innerText = "Enabling visual debugging...";
            break;
        }

        case "reset-breach-alert-last-displayed": {
            action.innerText = "Resetting breach alert last displayed...";
            const textContent = document.getElementById("mbw-text-content");
            const resetedDate = new Date(Number(params.get('date'))).toLocaleString()
            const breachesShownSites = await simpleStorageGet('breachesShownSites');
            const urlsIdList = Object.keys(breachesShownSites || {});
            const displayedUrlsList = urlsIdList.map((urlId) => {
                return breachesDB.find((breach) => breach.id === Number(urlId))?.site;
            });
            const sanitizedExample = DOMPurify.sanitize(params.get('example'));
            let breachAlertResetInfo = [
                `Last Displayed Date Was Rest To: ${resetedDate}`,
                `A Random Test URL From The DB: <a href="${sanitizedExample.startsWith('http') ? sanitizedExample : 'https://' + sanitizedExample}" target="_blank">${sanitizedExample}</a>`,
                `URLs That Were Already Displayed: ${displayedUrlsList.toString() || '-'}`,
            ]
            breachAlertResetInfo.forEach((info) => {
                // append each info as p tag to the text content
                const p = document.createElement("p");
                p.innerHTML = DOMPurify.sanitize(info);
                textContent.appendChild(p);
            });
            
            break;
        }

        case "permission-requests": {
            const operation = params.get('operation');
            const permission = params.get('permission');
            let result;
            action.innerText = `Permission request`;
            const textContent = document.getElementById("mbw-text-content");
        
            const btn = document.createElement("button");
            btn.innerText = `click here to ${operation} ${permission}`;
            btn.addEventListener('click', async () => {
                try {
                    if (operation === 'add') {
                        result = await requestAccessPermissions({ permissions: [permission] });

                    } else if (operation === 'remove') {
                        result = await removeAccessPermissions({ permissions: [permission] });
                    }

                    const p = document.createElement("p");
                    p.innerHTML = DOMPurify.sanitize(`${operation} result: ${result}`);
                    textContent.appendChild(p);

                    // add empty line
                    const br = document.createElement("br");
                    textContent.appendChild(br);

                    const p2 = document.createElement("p");
                    p2.innerHTML = DOMPurify.sanitize(`Current Permissions`);
                    textContent.appendChild(p2);

                    const currentPermissions = await chrome.permissions.getAll();
                    const stringifiedPermissions = JSON.stringify(currentPermissions, null, 4);
                    const pre = document.createElement("pre");
                    pre.innerHTML = DOMPurify.sanitize(stringifiedPermissions);
                    textContent.appendChild(pre);
                } catch (error) {
                    const pre = document.createElement("pre");
                    pre.innerHTML = DOMPurify.sanitize(error.message);
                    textContent.appendChild(pre);
                }
            });
            textContent.appendChild(btn);
            break;
        }

        case "simulate-cloud-message-response": {
            action.innerText = `Simulating cloud message response:`;
            const textContent = document.getElementById("mbw-text-content");

            // add new input field to 
            const inputfld = document.createElement("input");
            inputfld.type = "text";
            inputfld.placeholder = "Enter your message here";
            inputfld.style.width = "40rem";
            textContent.appendChild(inputfld);
            const btn = document.createElement("button");
            btn.innerText = "Send Message";
            
            btn.addEventListener('click', () => {
                const message = inputfld.value;
                if (!message) {
                    const pre = document.createElement("pre");
                    pre.innerHTML = DOMPurify.sanitize("Please enter a message");
                    textContent.appendChild(pre);
                    return;
                }
                chrome.runtime.sendMessage({
                    type: MSG_TEST_MY_ACCOUNT_RESPONSE,
                    payload: { message }
                }, function (response) {
                    if (!response || response.error) {
                        console.log("CLOUD TEST RES - Error: " + response.error);
                        return response.error;
                    } else {
                        console.log("CLOUD TEST RES: Done");
                        const stringifiedResponse = JSON.stringify(response, null, 4);
                        const pre = document.createElement("pre");
                        pre.innerHTML = DOMPurify.sanitize(stringifiedResponse);
                        textContent.appendChild(pre);
                        return response;
                    }
                });
            });
            textContent.appendChild(btn);
            textContent.appendChild(document.createElement("br"));

            break;
        }

        case "fp-checker-test": {
            action.innerText = "FP Checker Test";
            const textContent = document.getElementById("mbw-text-content");
            // add new input field to 
            const inputfld = document.createElement("input");
            inputfld.type = "text";
            inputfld.placeholder = "Enter rule ID to check";
            inputfld.style.width = "40rem";
            textContent.appendChild(inputfld);
            const btn = document.createElement("button");
            btn.innerText = "Check URL";
            btn.addEventListener('click', () => {
                const ruleId = inputfld.value;
                if (!ruleId) {
                    const pre = document.createElement("pre");
                    pre.innerHTML = DOMPurify.sanitize("Please enter a rule ID");
                    textContent.appendChild(pre);
                    return;
                }

                chrome.runtime.sendMessage({ type: MSG_TEST_FP_CHECKER, payload: { ruleId } }, function (response) {
                    if (!response || response.error) {
                        console.log("FPCT - Error: " + response.error);
                        return response.error;
                    } else {
                        console.log("FPCT: Done");
                        const stringifiedResponse = JSON.stringify(response, null, 4);
                        const pre = document.createElement("pre");
                        pre.innerHTML = DOMPurify.sanitize(stringifiedResponse);
                        textContent.appendChild(pre);
                        return response;
                    }
                });
            });
            textContent.appendChild(btn);
            textContent.appendChild(document.createElement("br"));
            break;
        }

        default:
            action.innerText = "Invalid action called";
            break;
    }
});

chrome.runtime.onMessage.addListener((message, _sender, sendResponse) => {
    if (message && message.type) {
        if (message.type === MSG_DEBUG_STAGING_CHANNEL_RESPONSE) {
            console.debug("OMAL: debugger-page.js received: ", message.type);
            let dbs = message.text.matchAll(/\(.+?\)/g).toArray();
            for (let db of dbs) {
                displayFeedbackMessage(db[0].slice(1,-1), "db-" + db.index);
            }
            sendResponse({ result: "Done" });
            return true;
        }
    }
    if (message.type === MSG_DEBUG_UPDATE_ERROR) {
        console.debug("OMAL: debugger-page.js received MSG_DEBUG_UPDATE_ERROR");
        displayFeedbackMessage(message.text, "update-error");
        sendResponse({ result: "Done" });
        return true;
    }
    return false;
});

function debugTestChannelUpdate() {
    displayFeedbackMessage("Starting test channel update", "start-test-channel-update");
    chrome.runtime.sendMessage({ type: MSG_DEBUG_TEST_CHANNEL }, function (response) {
        if (!response || response.error) {
            console.log("DTCU - Error: " + response.error);
            return response.error;
        } else {
            console.log("DTCU: Done");
            return response.result;
        }
    });
}

function checkUrl(url) {
    displayFeedbackMessage("Checking url...", "check-url");
    chrome.runtime.sendMessage({ type: MSG_DEBUG_CHECK_URL, url: url }, function (response) {
        if (!response || response.error) {
            console.log("CU - Error: " + response.error);
            return response.error;
        } else {
            console.log("CU: Done");
            dumpUrlResult(url, response.result);
            return response;
        }
    });
}

function addHeuristicRule(rule) {
    displayFeedbackMessage("Adding heuristic rule...", "add-heuristic-rule");
    chrome.runtime.sendMessage({ type: MSG_DEBUG_ADD_HEURISTIC_RULE, rule: rule }, function (response) {
        if (!response || response.error) {
            console.log("AHR - Error: " + response.error);
            return response.error;
        } else {
            console.log("AHR: Done");
            return response.result;
        }
    });
}

function debugStagingChannelUpdate() {
    displayFeedbackMessage("Starting staging channel update", "start-staging-channel-update");
    chrome.runtime.sendMessage({ type: MSG_DEBUG_STAGING_CHANNEL }, function (response) {
        if (!response) {
            console.log("DTCU - Error: No response");
            return "No response";
        } else
            if (response && response.error) {
                console.log("DTCU - Error: " + response.error);
                return response.error;
            } else {
                console.log("DTCU: Done");
                return response.result;
            }
    });
}

function dumpUrlResult(domain, {
    isInTop1m, whitelistedAd, isWhiteListedScam, isWhiteListedTracker,isHeuristicsScam, isHeuristicsPhishing
}) {
    if (isInTop1m) {
        displayFeedbackMessage(`Domain ${domain} is in top1m`, "domain-top1m");
    }
    else {
        displayFeedbackMessage(`Domain ${domain} is NOT in top1m`, "domain-top1m");
    }
    
    if (whitelistedAd) {
        displayFeedbackMessage(`Domain ${domain} is whitelisted for ads`, "domain-ad-check");
    }
    else {
        displayFeedbackMessage(`Domain ${domain} is NOT whitelisted for ads`, "domain-ad-check");
    }
    
    if (isWhiteListedTracker) {
        displayFeedbackMessage(`Domain ${domain} is whitelisted for trackers`, "domain-whitelist");
    }
    else {
        displayFeedbackMessage(`Domain ${domain} is NOT whitelisted for trackers`, "domain-whitelist");
    }
    if (isWhiteListedScam) {
        displayFeedbackMessage(`Domain ${domain} is whitelisted for scams`, "domain-scam");
    }
    else {
        displayFeedbackMessage(`Domain ${domain} is NOT whitelisted for scams`, "domain-scam");
    }
    if (isHeuristicsScam) {
        displayFeedbackMessage(`Domain ${domain} is detected as scam by heuristics`, "domain-heuristics-scam");
    }
    else {
        displayFeedbackMessage(`Domain ${domain} is NOT detected as scam by heuristics`, "domain-heuristics-scam");
    }
    if (isHeuristicsPhishing) {
        displayFeedbackMessage(`Domain ${domain} is detected as phishing by heuristics`, "domain-heuristics-phishing");
    }
    else {
        // eslint-disable-next-line max-len
        displayFeedbackMessage(`Domain ${domain} is NOT detected as phishing by heuristics`, "domain-heuristics-phishing");
    }
}

function displayFeedbackMessage(text, id) {
    console.log("DFM: " + text);
    let ul = document.getElementById("job-list");
    if (ul) {
        let li = document.createElement("li");
        li.innerText = text;
        if (id) {
            li.id = id;
        }
        ul.appendChild(li);
    }
}
